.schoolsList {
  @include flex(row, space-between, start, center);
  flex-wrap: wrap;
}
.result,
.noResult {
  text-align: center;
}

li.noResult {
  @include flex(column, center, center, center);
  text-transform: uppercase;
  min-height: auto;
  margin: 1rem auto;
  width: 50%;
  .resultName {
    font-size: 1.2rem;
  }
}

.result {
  width: 30%;
  margin: 1rem 0.5rem;
  position: relative;
  &Name {
    margin-bottom: 12px;
    font-size: 1.333rem;
    font-weight: 700;
  }
  &:focus {
    border: 3px solid $primaryColor;
  }
}

.bookmarkPosition {
  position: absolute;
  right: 6px;
  top: 6px;
  font-size: 1.111rem;
  &:hover {
    color: $activeColor;
  }
}

.bookmark {
  background: transparent;
  border: none;
}

.bookmarkIcon {
  color: $primaryColor;
  opacity: 0.8;
}
.result .generalButton {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (max-width: 1000px) {
  .result {
    width: calc(50% - 1rem);
  }
}
@media (max-width: 920px) {
  li.noResult {
    width: 100%;
  }
}
@media (max-width: 780px) {
  li.noResult {
    width: 80%;
  }
}

@media (max-width: 590px) {
  .result {
    width: 100%;
    margin: 0.5rem;
  }
}
