.noteContainer {
  background-image: url(../assets/notesBackground.jpg);
  background-repeat: tile;
  background-position: left top;
  padding-top: 20px;
  h2 {
    margin-top: 0;
    text-align: center;
    color: $black;
  }
  .wrapper {
    padding-bottom: 10px;
  }
}

.addSign {
  @include color(inherit, $black);
  display: flex;
  margin: 1rem 0;
  padding: 10px;
  font-size: 1.111rem;
  border-radius: 10px;
  background: linear-gradient(
    53deg,
    rgba(27, 65, 56, 1) 14%,
    rgba(7, 51, 48, 1) 31%,
    rgba(13, 27, 23, 1) 69%
  );
  color: $white;
  &:hover,
  &:focus {
    color: $primaryColor;
    background: $white;
  }
  p {
    padding-left: 10px;
  }
}

.notes {
  display: grid;
  text-align: center;
  grid-column-gap: 10px;
  grid-row-gap: 15px;
  grid-template-columns: 1fr 1fr;
}

.singleNote {
  margin-bottom: 1.5rem;
  .textWrapper {
    max-width: 100%;
  }
  p {
    margin-bottom: 0.7rem;
    word-wrap: break-word;
    overflow-wrap: break-word;
  }
  button {
    width: 25%;
    padding: 3px 2px;
    margin: 20px;
  }
  i {
    margin: 5px;
  }
  &:focus {
    border: 3px solid $primaryColor;
  }
}

.schoolName {
  margin-bottom: 15px;
  font-size: 1.222rem;
  font-weight: 700;
}

.bookmarks {
  display: flex;
  justify-content: center;
  h2 {
    display: inline-block;
    background: rgba(0, 0, 0, 0.7);
    color: $white;
    padding: 10px;
    border-radius: 10px;
  }
}

@media (max-width: 1000px) {
  .singleNote button {
    width: 40%;
    margin: 10px;
  }
  .bookmarks h2 {
    font-size: 1.1rem;
  }
}

@media (max-width: 780px) {
  .notes {
    grid-template-columns: 1fr;
  }
}
